<template>
  <div
    class="read"
    :style="{ backgroundColor: `${showhidden.night ? 'rgb(55, 55, 58)' : ''}` }"
  >
    <header v-show="showhidden.Setting">
      <router-link
        :to="{
          name: 'details',
          params: {
            id,
          },
        }"
      >
        <img src="@/assets/img/zjt.png" />
        <h4>返回</h4></router-link
      >
    </header>
    <div
      class="switchdata"
      :style="{ color: `${showhidden.night ? 'rgb(165, 165, 168)' : '#000'} ` }"
      @click="showhidden.Setting = !showhidden.Setting"
    >
      <h4
        :style="{
          color: `${showhidden.night ? 'rgb(165, 165, 168)' : '#000'} `,
        }"
      >
        {{ title }}
      </h4>
      <div
        class="diveledatas"
        :style="{ fontSize: `${fontSize / 2 / 37.5}rem` }"
      >
        <div class="content_box" v-for="(item, index) in content" :key="index">
          <div class="text" v-html="item"></div>
          <div class="img_box" @click.stop="showPopup(index + 1)">
            <img
              src="@/assets/img/dlpl.png"
              alt=""
              v-show="!paragraph_review_nums[index + 1]"
            />
            <span> {{ paragraph_review_nums[index + 1] }} </span>
            <img
              src="@/assets/img/plsl.png"
              alt=""
              v-show="paragraph_review_nums[index + 1]"
            />
          </div>
        </div>
      </div>
    </div>
    <van-popup v-model="show" position="right">
      <div class="comment_title">
        <img src="@/assets/img/zb.png" />
        <p>本段吐槽({{ comments.length }})</p>
      </div>
      <div class="comment_content">
        <div class="null_data" v-show="!comments.length">
          <img src="@/assets/img/wrtc.png" /><span>居然没人吐槽</span>
        </div>
        <div class="comment_info" v-for="item in comments" :key="item.id">
          <div class="top">
            <img
              :src="
                item.avatar
                  ? item.avatar
                  : 'https://m.jyacg.com/static/img/defaultHead.4ebeb21.png'
              "
            />
            <div>
              <p>
                <span>{{ item.nickname }}</span
                ><span class="level"> lv{{ item.level }} </span>
              </p>
              <p class="date">{{ item.cre_time }}</p>
            </div>
          </div>
          <div class="center">{{ item.content }}</div>
          <div class="bottom">
            <img
              @click="postlikeComment(item.id, 0)"
              src="@/assets/img/yhp.png"
              v-show="item.like_info.my_like_status == 1"
            />
            <img
              @click="postlikeComment(item.id, 1)"
              src="@/assets/img/hp.png"
              v-show="item.like_info.my_like_status != 1"
            />
            <span class="one">{{ item.like_info.like_num }}</span>
            <img
              @click="postlikeComment(item.id, 0)"
              src="@/assets/img/ycp.png"
              v-show="item.like_info.my_like_status == 2"
            />
            <img
              @click="postlikeComment(item.id, 2)"
              src="@/assets/img/cp.png"
              v-show="item.like_info.my_like_status != 2"
            />
            <span>{{ item.like_info.black_num }}</span>
          </div>
        </div>
      </div>
      <div class="redly">
        <input
          type="text"
          placeholder="吐个槽呗!"
          v-model.trim="mycomment"
        /><button @click="getadd_review">评论</button>
      </div>
    </van-popup>
    <div class="switch" v-show="showhidden.Setting">
      <div @click="showhidden.Directory = !showhidden.Directory">
        <img src="@/assets/img/ml.png" />
        <h4>目录</h4>
      </div>
      <div @click="showhidden.Font = !showhidden.Font">
        <img src="@/assets/img/zt.png" />
        <h4>字体</h4>
      </div>
      <div @click="showhidden.night = !showhidden.night">
        <img src="@/assets/img/yj.png" />
        <h4>夜间</h4>
      </div>
    </div>
    <div class="fontdata" v-show="showhidden.Font">
      <span>字号</span>
      <van-slider
        v-model="value"
        @change="onChange"
        step="12.5"
        active-color="#ee0a24"
        ><template #button>
          <div class="custom-button">{{ +fontSize }}</div>
        </template></van-slider
      >
    </div>
    <div class="turnpage">
      <a href="#"> <span @click="Iflogin(0, page - 1)">&lt;上一章</span></a>
      <a href="#"> <span @click="Iflogin(0, page + 1)">下一章 &gt;</span></a>
    </div>
    <van-popup v-model="showhidden.Directory" position="left" class="catadata">
      <h4>目录</h4>
      <div v-for="item in Directory" :key="item.id">
        <div class="juanming">{{ item.chapter_name }}</div>
        <div
          v-for="items in item.directory"
          :key="items.id"
          class="sird-title"
          @click="Iflogin(items.is_pay, items.page)"
        >
          <a href="#">
            {{ items.title }}
            <img v-if="items.is_pay" class="st" src="@/assets/img/st.png"
          /></a>
        </div>
      </div>
    </van-popup>
    <Iflogin></Iflogin>
    <MyLoading></MyLoading>
  </div>
</template>

<script>
import Iflogin from "@/components/Iflogin.vue";
import MyLoading from "@/components/MyLoading.vue";
export default {
  name: "Myread",
  props: ["id", "page"],
  components: { Iflogin, MyLoading },
  data() {
    return {
      content: [],
      title: "",
      fontSize: localStorage.getItem("fontSize"),
      paragraph_review_nums: {},
      review_content: [],
      show: false,
      comment: "",
      mycomment: "",
      showhidden: {
        Setting: false,
        night: false,
        Font: false,
        Directory: false,
      },
      Directory: [],
      value: (localStorage.getItem("fontSize") - 24) * 12.5,
      chapter: [],
    };
  },
  computed: {
    comments() {
      return this.review_content.filter((a) => {
        if (this.comment == a.paragraph) {
          return a;
        }
      });
    },
  },
  beforeRouteUpdate(to, from, next) {
    next();
    if (to.fullPath !== from.fullPath) {
      this.$nextTick(() => {
        this.getreview();
        this.getread();
      });
    }
  },
  methods: {
    Iflogin(is_pay, page) {
      if (this.page == this.chapter[0].page && this.page > page) {
        this.$toast("当前是第一章哟");
        return;
      }
      if (
        this.page == this.chapter[this.chapter.length - 1].page &&
        this.page < page
      ) {
        this.$toast("当前最后第一章哟");
        return;
      }
      if (page == this.page) {
        return;
      }
      if (is_pay) {
        if (!localStorage.getItem("api_token")) {
          this.$bus.$emit("Iflogin");
          return;
        }
      }

      this.$router.push({
        name: "read",
        query: {
          id: this.id,
          page,
        },
      });
    },
    onChange(value) {
      localStorage.setItem("fontSize", value / 12.5 + 24);
      this.fontSize = localStorage.getItem("fontSize");
    },
    showPopup(comments) {
      if (comments) {
        this.comment = comments;
      }
      this.show = true;
    },
    getadd_review() {
      if (!localStorage.getItem("api_token")) {
        this.$bus.$emit("Iflogin");
        return;
      }
      this.axios
        .get(
          `https://jyapi.jyacg.com/web/books/add_review?books_id=${this.id}&content=${this.mycomment}&section_id=${this.page}&paragraph=${this.comment}`,
          {
            headers: {
              authorization: localStorage.getItem("api_token"),
            },
          }
        )
        .then((res) => {
          if (res.status == 200) {
            this.getreview();
            this.mycomment = "";
          }
        });
    },
    getreview() {
      this.axios
        .get(
          `https://jyapi.jyacg.com/web/books/review?id=${this.id}&section_id=${this.page}&limit=1000`,
          {
            headers: {
              authorization: localStorage.getItem("api_token"),
            },
          }
        )
        .then((res) => {
          if (res.status == 200) {
            this.paragraph_review_nums = res.data.data.paragraph_review_nums;
            this.review_content = res.data.data.review_content;
          }
        });
    },
    getread() {
      this.$bus.$emit("MyLoading", true);
      this.axios
        .get(
          `https://jyapi.jyacg.com/web/books/read?books_id=${this.id}&page=${this.page}`,
          {
            headers: {
              authorization: localStorage.getItem("api_token"),
            },
          }
        )
        .then((res) => {
          if (res.status == 200) {
            if (!localStorage.getItem("api_token")) {
              if (res.page) {
                this.$router.go(-1);
                this.$bus.$emit("Iflogin");
                return;
              }
            }

            this.title = res.data.data.title;
            let a
            if (!res.data.data.is_col) {
              a =  "<p>" + res.data.data.content.replaceAll("<br />", "</p><p>") +"</p>"
            } else if (!res.data.data.is_free) {
              a = res.data.data.content.replaceAll("<br><br>", "</p><p>") 
            }else{
              a = res.data.data.content
            }
            this.content = a.replaceAll("</p>", "</p>#").split("#");
            this.content.splice(this.content.length - 1, 1);
            this.showhidden.Setting = false;
            this.showhidden.Directory = false;
            this.$bus.$emit("MyLoading", false);
          }
        });
    },
    postlikeComment(comment_id, type) {
      if (!localStorage.getItem("api_token")) {
        this.$bus.$emit("Iflogin");
        return;
      }
      this.axios
        .post(
          "https://jyapi.jyacg.com/api/users/likeComment",
          {
            comment_id,
            type,
          },
          {
            headers: {
              authorization: localStorage.getItem("api_token"),
            },
          }
        )
        .then((res) => {
          if (res.status == 200) {
            this.getreview();
          }
        });
    },
  },
  watch: {
    "showhidden.Setting"(a) {
      if (!a) {
        this.showhidden.Font = false;
      }
    },
  },
  mounted() {
    if (!localStorage.getItem("fontSize")) {
      localStorage.setItem("fontSize", 24);
      this.fontSize = localStorag.getItem("fontSize");
    }
    this.getreview();
    this.getread();
    this.axios
      .get(`https://jyapi.jyacg.com/web/books/directory?books_id=${this.id}`, {
        headers: {
          authorization: localStorage.getItem("api_token"),
        },
      })
      .then((res) => {
        if (res.status == 200) {
          this.Directory = res.data.data;
          this.chapter = res.data.data
            .map((a) => {
              return a.directory;
            })
            .flat(Infinity);
        }
      });
  },
};
</script>

<style lang="scss">
.read {
  width: 100%;
  padding-bottom: 50px;
  background-color: #f8f3e9;
  min-height: 667px;
  header {
    width: 375px;
