    a {
      display: flex;
      align-items: center;
    }
    img {
      height: 20px;
      margin-left: 37.5px;
      margin-right: 10px;
    }
    h4 {
      font-size: 15px;
      font-weight: bold;
      color: #fff;
    }
  }
  .switchdata {
    padding: 25px 40px 5px;
