      margin-bottom: 10px;
      img {
        width: 8px;
        height: 14px;
      }
      p {
        width: 251px;
        color: #409eff;
        font-size: 14px;
        text-align: center;
      }
    }
    .comment_content {
      overflow: auto;
      height: 560px;
      .comment_info {
        margin-top: 10px;
        padding-bottom: 10px;
