              text-align: center;
            }
            .date {
              color: #999;
              font-size: 12px;
            }
          }
        }
        .center {
          font-size: 12px;
          padding: 15px;
          width: 270px;
          line-height: 13px;
          color: #333;
        }
        .bottom {
          display: flex;
          justify-content: flex-end;
          padding: 0px 15px;
          span {
            font-size: 12px;
