        border-radius: 50px;
      }
    }
  }
  .switch {
    width: 375px;
    background-color: #666;
    height: 75px;
    display: flex;
    position: fixed;
    bottom: 0px;
    justify-content: space-around;
    align-items: center;
    div {
      width: 30px;
      display: flex;
      flex-direction: column;
