    }
  }
  .fontdata {
    background-color: #666;
    width: 337px;
    padding: 0px 18.75px;
    position: fixed;
    height: 40px;
    bottom: 75px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    span {
