<template>
  <van-loading v-show="show" size="24px">加载中...</van-loading>
</template>

<script>
export default {
  name: "MyLoading",
  data() {
    return {
      show: false,
    };
  },
  mounted() {
    this.$bus.$on("MyLoading", (a) => {
      this.show = a;
    });
  },
};
</script>

<style lang="scss" scoped>
.van-loading {
  width: 100%;
  height: 100%;
  position: fixed;
  background-color: #fff;
  align-items: center;
    justify-content: center;
  display: flex;
  line-height: 100%;
  text-align: center;
  position: 0.1;
  z-index: 10000;
  top: 0px;
}
</style>